import {Dispatch, Fragment, SetStateAction} from "react";
import {Button, Card, Container, Grid, Theme, Typography} from "@mui/joy";
import {Paper, useMediaQuery} from "@mui/material";


export default function WifiConfirmationForm(props: {
  state: Record<string, string>, setState: Record<string, Dispatch<SetStateAction<string>>>
}) {
  const { selectedNetwork, username, password, security, autoReconfigure, confirming } = props.state
  const { setSelectedNetwork, setUsername, setPassword, setSecurity, setAutoReconfigure, setConfirming } = props.setState

  const isMobile = useMediaQuery((theme: Theme) => theme.breakpoints.down('md'));

  return (
    <Fragment>
      <Card>
        {confirming !== "done" && <Typography textAlign={'center'}>
          Please confirm that you would like to connect to the following network:
        </Typography>}
        <Container style={{margin: 20}}>
          <Typography>
            <b>Security: </b>
            {props.state.security}
          </Typography>
          <Typography>
            <b>Network Name: </b>
            {props.state.selectedNetwork}
          </Typography>
          {security === "EAP" && <Typography>
            <b>Username: </b>
            {props.state.username}
          </Typography>}
          <Typography>
            <b>{security === 'WPA' ? 'Pre-Shared Key (PSK) or Passphrase' : 'Password'}: </b>
            {props.state.password}
          </Typography>
          <Typography>
            <b>Auto Reconfigure: </b>
            {props.state.autoReconfigure ? "Yes" : "No"}
          </Typography>
        </Container>
      </Card>
      {confirming !== 'done' && <Grid container justifyContent={"center"} spacing={2}>
        <Grid xs={12} md={6}>
          <Button onClick={() => setConfirming('')} color={"neutral"} fullWidth>Back</Button>
        </Grid>
        <Grid xs={12} md={6} order={isMobile ? -1 : 2}>
          <Button onClick={() => setConfirming('done')} fullWidth>Confirm</Button>
        </Grid>
      </Grid>}
      {confirming === 'done' && <Typography textAlign={'center'}>
        Please wait while your device connects to the network...
      </Typography>}
    </Fragment>
  )
}