import {Dispatch, Fragment, SetStateAction} from "react";
import {Autocomplete, Button, Input, Radio, RadioGroup, Sheet, Switch, Typography} from "@mui/joy";
import WifiIcon from "@mui/icons-material/Wifi";
import PersonIcon from "@mui/icons-material/Person";
import LockIcon from "@mui/icons-material/Lock";

const OPTIONS = ['WPA', "WPA2", "EAP"]

export default function WifiSelectionForm(props: {
  networks: string[], state: Record<string, string>, setState: Record<string, Dispatch<SetStateAction<string>>>
}) {
  const { selectedNetwork, username, password, security, autoReconfigure, confirming } = props.state
  const { setSelectedNetwork, setUsername, setPassword, setSecurity, setAutoReconfigure, setConfirming } = props.setState

  return (
    <Fragment>
      <p style={{marginTop: 0, marginBottom: -20, textAlign: 'center'}}>Select Wifi Configuration</p>
      <RadioGroup orientation={"horizontal"} sx={{margin: "auto"}} value={security}
                  onChange={(e) => setSecurity(e.target.value)}>
        {OPTIONS.map((option) =>
          <Sheet key={option} sx={{
            p: 1,
            m: 1,
            borderRadius: 'lg',
            boxShadow: 'sm',
          }}>
            <Radio label={option} value={option} overlay disableIcon slotProps={{
              label: ({checked}) => ({
                sx: {
                  fontWeight: 'lg',
                  fontSize: 'md',
                  color: checked ? 'white' : 'black',
                },
              }),
              action: ({checked}) => ({
                sx: (theme) => ({
                  ...(checked && {
                    '--variant-borderWidth': '0px',
                    '&&': {
                      // && to increase the specificity to win the base :hover styles
                      // borderColor: theme.vars.palette.primary[500],
                      backgroundColor: '#23366D',
                    },
                  }),
                }),
              }),
            }}/>
          </Sheet>
        )}
      </RadioGroup>
      <Autocomplete placeholder={"WiFi Name"} options={props.networks} startDecorator={<WifiIcon/>} freeSolo
                    value={selectedNetwork} onInputChange={(e, v) => {
        setSelectedNetwork(v || "")
      }} />
      {security === 'EAP' && <Input placeholder={"Username"} startDecorator={<PersonIcon/>} value={username}
                                    onChange={(e) => setUsername(e.target.value)}/>}
      <Input placeholder={security === 'WPA' ? "Pre-Shared Key (PSK) or Passphrase" : "Password"}
             startDecorator={<LockIcon/>}
             value={password} onChange={(e) => setPassword(e.target.value)}/>
      <Typography endDecorator={
        <Switch sx={{marginLeft: "10px"}} checked={autoReconfigure === "1"} onChange={(e) => {
          setAutoReconfigure(e.target.checked ? "1" : "0")
        }} />
      }>Auto-Reconfiguration</Typography>
      <Button color={"primary"} onClick={() => setConfirming("y")}>Connect to WiFi</Button>
    </Fragment>
  )
}