import {Component, useEffect, useState} from "react";
import {
  Autocomplete,
  Button,
  Container,
  FormControl,
  FormLabel, Grid,
  Input,
  Radio,
  RadioGroup,
  Sheet,
  styled,
  TextField
} from "@mui/joy";

import WifiSelectionForm from "./WifiSelectionForm";
import WifiConfirmationForm from "./WifiConfirmationForm";

export default function WifiSetup() {

  const [networks, setNetworks] = useState(["Looking for networks..."])
  const [selectedNetwork, setSelectedNetwork] = useState("")
  const [username, setUsername] = useState("")
  const [password, setPassword] = useState("")
  const [security, setSecurity] = useState("WPA2")
  const [autoReconfigure, setAutoReconfigure] = useState("1")
  const [confirming, setConfirming] = useState("")
  const [hostname, setHostname] = useState("thisdoesnotexist.local")

  const state = {selectedNetwork, username, password, security, autoReconfigure, confirming}
  const setState = {setSelectedNetwork, setUsername, setPassword, setSecurity, setAutoReconfigure, setConfirming}

  useEffect(() => {
    fetch("/get_wifi_networks")
      .then(res => res.json())
      .then(json => {
        setNetworks(json)
        // console.log(json)
      })

    fetch("/get_hostname")
      .then(res => res.text())
      .then(text => {
        text = text.replace(/"/g, '');
        setHostname(text + ".local")
      })

    fetch("/get_auto_reconfigure")
      .then(res => res.text())
      .then(text => {
        text = text.replace(/"/g, '').replace(/\n/g, '');
        setAutoReconfigure(text)
      })

  }, [])

  useEffect(() => {
    fetch("/update_auto_reconfigure", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        autoReconfigure: parseInt(autoReconfigure)
      }),
    }).then((response) => {
      response.text().then((response) => {
        const res = response as unknown as string;
        // console.log(res);
      });
    });
  }, [autoReconfigure]);

  useEffect(() => {
    if (confirming !== "done") return;

    fetch("/update_credentials", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        ssid: selectedNetwork,
        user: username,
        pass: password,
        security: security
      }),
    }).then((response) => {
      response.text().then((response) => {
        const res = response as unknown as string;
        // console.log(res);
        // if (res.startsWith("Token: ")) {
        //   localStorage.setItem("token", res.split(" ")[1]);
        //   props.setLoggedIn(true);
        // } else {
        //   setError(res);
        // }
      });
    });
  }, [confirming]);

  useEffect(() => {
    const redirectIfReady = () => {
      fetch(`http://${hostname}/`, { mode: 'no-cors' }).then((response) => {
        window.location.href = `http://${hostname}/`;
      }).catch((e) => {
        setTimeout(redirectIfReady, 1000);
      });
    }

    if (confirming === "done") {
      setTimeout(redirectIfReady, 15000);
    }
  }, [confirming]);

  return (
    <SetupContainer>
      <StyledForm id="asynchronous-demo" size={"lg"} style={{paddingTop: 30}}>
        { !confirming ?
          <WifiSelectionForm networks={networks} state={state} setState={setState}/> :
          <WifiConfirmationForm state={state} setState={setState}/>
        }
      </StyledForm>
    </SetupContainer>
  )
}

const SetupContainer = styled("div")({
  display: "flex",
  alignItems: "center",
  justifyContent: "space-evenly",
  flexDirection: "column",
})

const StyledForm = styled(FormControl)({
  gap: "20px",
  width: "100%",
})