import {Card, styled, Tooltip, Typography} from "@mui/joy";
import CircleIcon from '@mui/icons-material/Circle';
import {useEffect, useState} from "react";

export default function WifiStatus () {
  const [ssid, setSsid] = useState('null');
  const [ping, setPing] = useState(null);
  const [mac, setMac] = useState(null);

  useEffect(() => {
    const getMacAddress = async () => {
      const response = await fetch('/get_mac');
      const json = await response.json();
      setMac(json.mac);
    }
    const getWifiStatus = async () => {
      const response = await fetch('/get_wifi_info');
      const json = await response.json();
      setSsid(json.ssid);
      setPing(json.ping);

      setTimeout(getWifiStatus, 3000);
    }

    getMacAddress();
    getWifiStatus();
  }, []);

  return (
    <Card size={"sm"} sx={{marginTop: '10px'}}>
      <StatusContainer>
        { ssid ?
          <Typography>Currently connected to: {ssid}
            <Tooltip title={"Using MAC: " + mac}>
              <a href={`https://sensorweb.us:3000/d/oY7BsgVMz/sleep-monitoring?orgId=1&from=now-1m&to=now&refresh=5s` +
                `&var-mac=${mac}&var-name=vitalsigns`} target={"_blank"} rel={"noopener noreferrer"}>
                View Data
              </a>
            </Tooltip>
          </Typography> :
          <Typography>Not connected to a network</Typography>
        }
        <Typography endDecorator={<CircleIcon sx={{color: ping ? 'green' : 'red'}} />} sx={{whiteSpace: 'nowrap'}}>
          {ping ? ping + 'ms' : 'No Ping'}
        </Typography>
      </StatusContainer>
    </Card>
  )
}

const StatusContainer = styled("div")({
  display: "flex",
  alignItems: "center",
  justifyContent: "space-between",
  flexDirection: "row",
  "& #text": {
    whiteSpace: 'nowrap',
  },
  "& a": {
    color: '#234B6C',
    cursor: 'pointer',
    textDecoration: 'underline',
    marginLeft: '5px',
    whiteSpace: 'nowrap',
  }
})